@charset "utf-8";
/* CSS Document */



body,
td,
th,
p{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
  line-height: 1.5;
}
body {
  background-color: #fdfdfd;
  margin: 0;
  position:relative;
}
ul, 
li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
}
a {
  color: orange;
  text-decoration: none;
}
a:hover, a:focus {
  color: #3397db;
  text-decoration: none;
}
a img{
  border: none 0px #fff;
}
h1, 
h2, 
h3, 
h4,
h5, 
h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 20px;
}
article,
aside, 
canvas, 
details,
figcaption, 
figure, 
footer, 
header, 
hgroup, 
menu,
nav, 
section {
  display: block;
}
#maincontent {
  background-color: #f3f3f3;
  min-height: 100%;
}

#footer {
  background-color: #444;
  color: #aaa;
  font-size: 8pt;
  letter-spacing: 1px;
  padding: 25px;
  text-align: center;
  text-transform: uppercase;
}
/* ====================== Navigation ====================== */
.main-nav {
  display: flex;
  width: 100%;
  height: 80px;
  background-color: #252831;
  text-align:center;
}
.main-nav h1 {
  margin: auto;
}
.main-nav h1 a {
  color: #fff;
  font-size: 14pt;
  font-weight: 200;
  letter-spacing: 10px;
  text-transform: uppercase;
}
#breadcrumb {
  padding: 10px 40px 16px;
  list-style: none;
  background-color: #eee;
  font-size: 17px;
  margin: 0;
  padding-left: 15px;
}

/* Display list items side by side */
#breadcrumb li {
  display: inline;
}

/* Add a slash symbol (/) before/behind each list item */
#breadcrumb li+li:before {
  padding: 8px;
  color: black;
  content: "/\00a0";
}

/* Add a color to all links inside the list */
#breadcrumb li a {
  color: #0275d8;
  text-decoration: none;
}

/* Add a color on mouse-over */
#breadcrumb li a:hover {
  color: #01447e;
  text-decoration: underline;
}
/* ====================== Map ====================== */
#map {
  height: 400px;
  width: 100%;
  background-color: #ccc;
}
/* ====================== Restaurant Filtering ====================== */
.filter-options {
  width: 100%;
  background-color: #3397DB;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  display: flex;
  padding-top: 10px;
}
@media(min-width: 371px){
  .filter-options {
    padding: 0;
  }
}
.filter-options h2 {
  color: white;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1;
  margin: 0 20px;
}
.filter-options select {
  background-color: white;
  border: 1px solid #fff;
  font-family: Arial,sans-serif;
  font-size: 11pt;
  height: 35px;
  letter-spacing: 0;
  margin: 10px;
  padding: 0 10px;
  width: 200px;
  max-width: 100%;
}

/* ====================== Restaurant Listing ====================== */
#restaurants-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
  background-color: #f3f3f3;
  list-style: outside none none;
  text-align: center;
  padding: 0;
}
#restaurants-list li {
  background-color: #fff;
  /* border: 2px solid #ccc; */
  font-family: Arial,sans-serif;
  margin: 15px;
  /* min-height: 380px; */
  /* padding: 0 30px 25px; */
  text-align: left;
  width: 100%;
  padding-bottom: 10px;
  box-shadow: 1px 1px 8px 0 rgba(46,61,73,.2);
}
@media(min-width: 591px){
  #restaurants-list li {
    width: calc(50% - 30px);
  }
}
@media(min-width: 1025px){
  #restaurants-list li {
    width: calc(33.33% - 30px);
  }
}
#restaurants-list .restaurant-img {
  background-color: #ccc;
  display: block;
  margin: 0;
  max-width: 100%;
  /* min-height: 248px; */
  min-width: 100%;
}
#restaurants-list li h2 {
  color: #f18200;
  font-family: Arial,sans-serif;
  font-size: 14pt;
  font-weight: 200;
  letter-spacing: 0;
  line-height: 1.3;
  margin: 10px 0 10px 10px;
  text-transform: uppercase;
}
#restaurants-list p {
  margin: 0;
  font-size: 11pt;
  
  margin-left: 10px;
}
#restaurants-list li a {
  background-color: orange;
  border-bottom: 3px solid #eee;
  color: #fff;
  display: inline-block;
  font-size: 10pt;
  margin: 15px 0 0;
  padding: 8px 30px 10px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  
  margin-left: 10px;
}

/* ====================== Restaurant Details ====================== */
.inside {
  background-color: #f3f3f3;
}
.inside header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10000;
}
.inside #map-container {
  background: blue none repeat scroll 0 0;
  height: 400px;
  position: relative;
  top: 128px;
  width: 100%;
}
.inside #map {
  background-color: #ccc;
  height: 100%;
  width: 100%;
}
.inside #footer {
  bottom: 0;
  position: absolute;
  width: calc(100% - 50px);
}
#restaurant-name {
  color: #f18200;
  font-family: Arial,sans-serif;
  font-size: 16pt;
  font-weight: 200;
  letter-spacing: 0;
  margin: 30px 0 20px;
  text-transform: uppercase;
  line-height: 1.1;
}
#restaurant-img {
  width: 100%;
}
#restaurant-address {
  font-size: 12pt;
  margin: 10px 0px;
}
#restaurant-cuisine {
  background-color: #333;
  color: #ddd;
  font-size: 12pt;
  font-weight: 300;
  letter-spacing: 10px;
  margin: 0 0 20px;
  padding: 2px 0;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
  padding: .5em 0;
}
#restaurant-container, #reviews-container {
  border-bottom: 1px solid #d9d9d9;
  border-top: 1px solid #fff;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% - 30px);
  padding-top: 133px;
}
#reviews-container {
  padding-top: 30px;
}
#reviews-container h2 {
  color: #f58500;
  font-size: 16pt;
  font-weight: 300;
  letter-spacing: -1px;
  padding-bottom: 1pt;
}
#reviews-list {
  margin: 0;
  padding: 0;
}
#reviews-list li {
  background-color: #fff;
  border: 2px solid #f3f3f3;
  display: block;
  list-style-type: none;
  margin: 0 0 30px;
  overflow: hidden;
  padding: 20px;
  position: relative;
  width: calc(100% - 40px);
}

#reviews-list li:last-child {
  margin-bottom: 113px;
}

#reviews-list li p {
  margin: 0 0 10px;
}
#restaurant-hours {
  padding-bottom: 30px; 
  width: 100%;
}

#restaurant-hours td {
  color: #666;
}

/* Media Queries for restaurant detailsÂ */

@media(min-width: 400px){
  #restaurant-name, #reviews-container h2  {
    font-size: 20pt;
  }

  #restaurant-hours td ,#reviews-list li p, #breadcrumb li{
    font-size: 12pt;
  }

  #restaurant-address {
    font-size: 16pt;
  }
}

@media(min-width: 700px){
  #restaurant-content {
    content: "";
    clear: both;
    display: table;
    padding-bottom: 30px;
  }
  #restaurant-box {
    float: left;
    width: 50%;
  }
  #restaurant-box:last-child {
    float: left;
    width: calc(50% - 10px);
    margin-left: 10px;
    margin-top: 58px;
  }
}

@media(min-width: 900px){
  #breadcrumb {
    width: calc(50% - 55px);
  }

  .maincontent--inside{
    max-width: 1200px;
    margin: 0 auto;
  }

  .inside #map-container {
    background: blue none repeat scroll 0 0;
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    width: 50%;
  }
  #restaurant-container, #reviews-container {
    width: calc(50% - 30px);
    padding-left: 15px;
    padding-right: 15px;
    margin: 0;
  }
  #restaurant-content {
    display: block;
    padding-bottom: 0;
  }
  #restaurant-box {
    float: none;
    width: 100%;
  }
   #restaurant-box:last-child {
    float: none;
    width: 100%;
    margin-top: 30px;
  }
}
